@use "../../assets/scss/util" as *;

.Nav {
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: var(--white);

  position: fixed;
  width: 100vw;
  top: 0;

  &__BrandLink {
    block-size: 2rem;
    inline-size: 2rem;
  }

  &__menu {
    display: flex;
    gap: 2rem;
    color: var(--grey-500);

    @include breakpoint(medium) {
      font-size: 0.8rem;
    }
  }
}
